import DeepData from "../../../assets/js/formate.js";
export default {
  name:'game_notice',
  data() {
    return {
      title: "新增",
      searchinput: "",
      currentPage: 1,
      limit: 10,
      total: 0,
      visible: false,
      imageUrl: "",
      serveUrl: "",
      tableData: [],
      form: {
        Title: "",
        type_id: 1,
        Description: "",
        ImageUrl: "",
        Tag: 1,
        StartTime: "",
        EndTime: "",
        Sort: "",
        Status: 1
      },
      rules: {
        Title: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        Description: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        type_id: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        StartTime: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ],
        EndTime: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ],
        Sort: [
          {
            required: true,
            message: "必填项不能为空,只能输入数字",
            trigger: "blur"
          }
        ],
        Status: [
          { required: true, message: "必填项不能为空", trigger: "blur" }
        ]
      },
      initForm: {
        Title: "",
        Description: '',
        type_id: 1,
        ImageUrl: "",
        Tag: 1,
        StartTime: "",
        EndTime: "",
        Sort: "",
        Status: 1
      },
      titleData:[
        {
          prop:"AnnouncementId",
          label:"ID",
        },
        {
          prop:"Title",
          label:"公告标题",
        },
        {
          prop:"Tag",
          label:"标签",
        },
        {
          prop:"StartTime",
          label:"公告开始时间",
        },
        {
          prop:"EndTime",
          label:"公告结束时间",
        },
        {
          prop:"type_id",
          label:"公告类型",
        },
        {
          prop:"Sort",
          label:"排序",
        },
        {
          prop:"Status",
          label:"状态",
        },
      ]
    };
  },
  created() {
    this.initdata({
      page: this.currentPage,
      limit: this.limit,
      title: this.searchinput
    });
  },

  methods: {
    //限制用户上传的图片格式和大小
    beforeAvatarUpload(file) {
      // console.log(file);
      if (file) {
        this.form.ImageUrl = URL.createObjectURL(file);
      }
    },
    upLoad(file) {
      // console.log(file);
      const formData = new FormData();
      formData.append("filename", file.file);
      formData.append("types", 1);
      this.$http.post("v1/backend/upload", formData).then(data => {
        // console.log(data);
        if (data.data.code === 1 && data.data.msg === "ok") {
          this.serveUrl = data.data.path;
          this.form.ImageUrl = URL.createObjectURL(file);
        }
      });
    },

    //添加
    add() {
      this.editForm("新增", true, DeepData(this.initForm));
    },

    //搜索
    search() {
      if (this.searchinput === "") {
        this.$message({
          type: "warning",
          message: "请输入你要搜索的标题!"
        });
      }
      this.currentPage = 1;
      this.limit = 10;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },

    //页容量发生变化
    handleSizeChange(num) {
      // console.log(num);
      this.limit = num;
      this.currentPage = 1;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },

    //页码发生变化
    handleCurrentChange(pagenum) {
      // console.log(pagenum);
      this.currentPage = pagenum;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },

    //表格编辑
    handleEdit(row) {
      // console.log(row);
      this.form = this.formateNum(DeepData(row));

      this.form.StartTime = this.data(row.StartTime);
      this.form.EndTime = this.data(row.EndTime);
      // console.log(this.form);
      this.editForm("编辑", true, this.form);
    },

    //表格删除
    handleDelete(y, row) {
      // console.log(y, row.id);
      this.$confirm("确认删除吗？", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          //2.发送请求在后台删除数据
          let { data } = await this.$http.HallFunConfig.DeleteGameNotice({
            id: row.AnnouncementId
          });
          // console.log(data);
          if (data.code === 1 && data.msg === "ok") {
            this.initdata({
              page: this.currentPage,
              limit: this.limit,
              title: this.searchinput
            });
          }
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //表单提交
    onSubmit(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          this.form.ImageUrl = this.serveUrl;
          // console.log(this.form);

          if (type === "新增") {
            // console.log(this.form);
            let { data } = await this.$http.HallFunConfig.PostGameNotice(
              this.form
            );
            // console.log(data);

            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                title: this.searchinput
              });
            }
          } else if (type === "编辑") {
            // console.log(this.form);
            if (this.form.ImageUrl === "") {
              this.form.ImageUrl = "ImageUrl";
            }
            let { data } = await this.$http.HallFunConfig.PutGameNotice(
              this.form
            );
            // console.log(data);

            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                title: this.searchinput
              });
            }
          }
          this.editForm("添加", false, {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    editForm(title, visible, form) {
      this.title = title;
      this.visible = visible;
      this.form = form;
      // console.log(this.form);
    },

    formateData(res) {
      res.forEach(item => {
        item.type_id = item.Description  ? "文字" : "图片";
        item.Status = item.Status === 1 ? "展示" : "隐藏";
        if (item.Tag === 1) {
          item.Tag = "没有标签";
        } else if (item.Tag === 2) {
          item.Tag = "新";
        } else {
          item.Tag = "热门";
        }
      });
      return res;
    },

    formateNum(item) {
      if (
        typeof item.type_id === "number" &&
        typeof item.Tag === "number" &&
        typeof item.Status === "number"
      )
        return;

      item.type_id = item.type_id === "文字" ? 1 : 2;
      item.Status = item.Status === "展示" ? 1 : 2;
      if (item.Tag === "没有标签") {
        item.Tag = 1;
      } else if (item.Tag === "新") {
        item.Tag = 2;
      } else {
        item.Tag = 3;
      }

      return item;
    },

    data(time) {
      let long1 = Date.parse(time);
      let long2 = new Date(long1).getTime();
      return long2;
    },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetGameNotice(params);
      let fres = this.formateData(data.data);
      this.tableData = fres;
      this.total = data.total;
      // console.log(data);
    }
  }
};



